const convertISBN10to13 = (isbn10: string) => {
    // Remove any hyphens from ISBN-10
    isbn10 = isbn10.replace(/-/g, '');
  
    // Add the prefix "978" to the beginning
    const isbn13Prefix = "978";
    // console.log(isbn10.slice(0, -1));
    
    const isbn13 = isbn13Prefix + isbn10.slice(0, -1);
//   console.log(isbn13);
  
    // Calculate the check digit for ISBN-13
    const checkDigit = calculateCheckDigit(isbn13);
  
    return isbn13 + checkDigit;
  }


